.header {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2px;
}
img {
  width: 180px;
}
.myForm {
  display: flex;
  justify-content: center;
  background: rgb(64, 100, 144);
  width: 100%;
  height: auto;
  margin-top: 20px;
}
.subForm {
  background-color: white;
  margin-top: 30px;
  margin-bottom: 60px;
  border-radius: 10px;
  width: 70vw;
  height: auto;
}

p {
  font-size: 16px;
  margin: 30px;
  color: #4b4b4b;
}

.displayError {
  display: block;
  background-color: #f2dede;
  margin: 30px;
  color: #b94a48;
  width: 63vw;
  height: auto;
  font-family: 'Times New Roman';
  padding: 15px;
}
.dontDisplay {
  display: none;
}
form {
  margin: 30px;
}
label {
  font-weight: 400;
}
input {
  width: 63vw;
  height: 30px;
}
.myBtn {
  background-color: #579af6;
  color: white;
  font-size: 15px;
  border: 1px solid #579af6;
  width: 82.22px;
  height: 45px;
  border-radius: 5px;
}

@media screen and (max-width: 768px) {
  .subForm {
    background-color: white;
    margin-top: 30px;
    border-radius: 10px;
    width: 87vw;
  }
  input {
    width: 70vw;
    height: 30px;
  }
}